.sign-form__button {
  margin: 216px 0 15px;
  padding: 14px 84px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  border-radius: 2px;
  border: 0;
  opacity: 1;
  cursor: pointer;
  transition: opacity 0.6s linear 0.1s;
}

.sign-form__button:hover {
  opacity: 0.85;
}
