.sing-form__input {
  width: 358px;
  margin: 0 0 30px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  background-color: black;
  border: 0;
  border-bottom: 2px solid #cccccc;
  color: #cccccc;
}

.sing-form__input:nth-of-type(2) {
  margin: 0;
}

.sing-form__input:focus {
  background-color: black;
  outline: none;
}
